header {
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
    position: fixed;
    z-index: 200;
    width: 100%;
    height: 60px;
    max-width: none !important;
    padding: 0px;
    margin-bottom: 60px;
    background-color: #fff;

    .header-links {
        list-style: none;
        margin: 0;
        padding: 5px 0 0;
        text-align: center;

        li {
            display: inline-block;
            padding: 14px 15px;
            font-family: TATSanaChon;
            font-size: 14px;
            font-weight: bold;

            a {
                text-decoration: none;
                color: #333333;
                font-weight: bold;
                background-color: transparent;
                cursor: pointer;
                position: relative;
            }

            a:hover {
                color: #213A8F;
            }

            a:after {
                content: "";
                position: absolute;
                width: 100%;
                height: 6px;
                bottom: -14px;
                left: 0;
                background-color: #213A8F;
                visibility: hidden;
                -webkit-transform: scaleX(0);
                transform: scaleX(0);
                -webkit-transition: all 0.3s ease-in-out 0s;
                transition: all 0.3s ease-in-out 0s;
            }

            @media (max-width: 769px) {
                a:after {
                    visibility: hidden;
                }
            }

            @media (max-width: 577px) {
                a:after {
                    visibility: hidden;
                }
            }

            a:hover:after {
                visibility: visible;
                -webkit-transform: scaleX(1);
                transform: scaleX(1);
            }

            @media (max-width: 769px) {
                a:hover:after {
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                }
            }

            @media (max-width: 577px) {
                a:hover:after {
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                }
            }
        }
    }

    @media (max-width: 577px) {
        .header-links {
            display: none;
        }
    }

    .header-link-mobile {
        display: none;
        text-align: right;
        cursor: pointer;
    }

    @media (max-width: 577px) {
        .header-link-mobile {
            display: block;
        }
    }
}


.navbar-brand {
    width: 35px;
    height: 48px;
    padding: 0;

    img {
        width: 100%;
        height: 100%;
    }
}


.navbar-toggler {
    border: none;
}

.navbar-toggler:focus {
    outline: 0;
}

.animated-icon1,
.animated-icon2,
.animated-icon3 {
    width: 30px;
    height: 20px;
    position: relative;
    margin: 0px;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
}

.animated-icon1 span,
.animated-icon2 span,
.animated-icon3 span {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    -moz-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
}

.animated-icon1 {
    span {
        background: #333333;
    }

    span:nth-child(1) {
        top: 0px;
    }

    span:nth-child(2) {
        top: 10px;
    }

    span:nth-child(3) {
        top: 20px;
    }

}

.animated-icon1.open {
    span:nth-child(1) {
        top: 11px;
        -webkit-transform: rotate(135deg);
        -moz-transform: rotate(135deg);
        -o-transform: rotate(135deg);
        transform: rotate(135deg);
    }

    span:nth-child(2) {
        opacity: 0;
        left: -60px;
    }

    span:nth-child(3) {
        top: 11px;
        -webkit-transform: rotate(-135deg);
        -moz-transform: rotate(-135deg);
        -o-transform: rotate(-135deg);
        transform: rotate(-135deg);
    }
}

.navbar {
    background: #fff;
}