header {
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  z-index: 200;
  width: 100%;
  height: 60px;
  max-width: none !important;
  padding: 0px;
  margin-bottom: 60px;
  background-color: #fff; }
  header .header-links {
    list-style: none;
    margin: 0;
    padding: 5px 0 0;
    text-align: center; }
    header .header-links li {
      display: inline-block;
      padding: 14px 15px;
      font-family: TATSanaChon;
      font-size: 14px;
      font-weight: bold; }
      header .header-links li a {
        text-decoration: none;
        color: #333333;
        font-weight: bold;
        background-color: transparent;
        cursor: pointer;
        position: relative; }
      header .header-links li a:hover {
        color: #213A8F; }
      header .header-links li a:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 6px;
        bottom: -14px;
        left: 0;
        background-color: #213A8F;
        visibility: hidden;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s; }
      @media (max-width: 769px) {
        header .header-links li a:after {
          visibility: hidden; } }
      @media (max-width: 577px) {
        header .header-links li a:after {
          visibility: hidden; } }
      header .header-links li a:hover:after {
        visibility: visible;
        -webkit-transform: scaleX(1);
        transform: scaleX(1); }
      @media (max-width: 769px) {
        header .header-links li a:hover:after {
          -webkit-transform: scaleX(0);
          transform: scaleX(0); } }
      @media (max-width: 577px) {
        header .header-links li a:hover:after {
          -webkit-transform: scaleX(0);
          transform: scaleX(0); } }
  @media (max-width: 577px) {
    header .header-links {
      display: none; } }
  header .header-link-mobile {
    display: none;
    text-align: right;
    cursor: pointer; }
  @media (max-width: 577px) {
    header .header-link-mobile {
      display: block; } }

.navbar-brand {
